                v-model="nickname"
                label="Ïù¥Î¶Ñ"
                type="text"
                :rules="nameRules"
                required
            />
            <v-text-field
                v-model="password"
                label="ÎπÑÎ∞ÄÎ≤àÌò∏"
                type="password"
                :rules="passwordRules"
                required
            />
            <v-text-field
