        v=> !!v || 'Ïù¥Î©îÏùºÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.',
        v=> /.+@.+/.test(v) || 'Ïù¥Î©îÏùºÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.',
      ],
      nameRules: [
        v => !!v || 'Ïù¥Î¶ÑÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.',
      ],
      passwordRules: [
        v => !!v || 'Ìå®Ïä§ÏõåÎìúÎäî ÌïÑÏàòÏûÖÎãàÎã§.',
      ],
      passwordCheckRules: [
        v => !!v || 'Ìå®Ïä§ÏõåÎìú ÌôïÏù∏ÏùÄ ÌïÑÏàòÏûÖÎãàÎã§.',
        v =>  v === this.password || 'ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.',
      ]
    }
  },
