import Vue from "vue"
import Vuex from "vuex"
import {router} from "@/routes/route";
import axios from "axios";
import { getMyInfoApi } from "../apis/index";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        userInfo: localStorage.token === null ? null : localStorage.token,
        isLogin: false,
        isLoginError: false
    },
    mutations: {
        // isloginÏùÑ trueÎ°ú ÎßåÎì§ÎçòÍ∞Ä isloginerrorÎ•º trueÎ°ú ÎßåÎì§ÎçòÍ∞Ä

        //Î°úÍ∑∏Ïù∏Ïù¥ ÏÑ±Í≥µÌñàÏùÑ Îïå
        loginSuccess(state, payload){
            state.isLogin = true
            state.isLoginError = false
            state.userInfo = payload
        },
        //Î°úÍ∑∏Ïù∏Ïù¥ Ïã§Ìå®ÌñàÏùÑ Îïå
        loginError(state) {
            state.isLogin = false
            state.isLoginError = true
        },
        logout(state){
            state.isLogin = false
            state.isLoginError = false
            state.userInfo = null
        }
    },
    actions: {
        // Î°úÍ∑∏Ïù∏ ÏãúÎèÑ
        // commit ÏÇ¨Ïö© Í∑∏Î¶¨Í≥† payload Ïù∏ÏûêÍ∞íÏù¥ Îì§Ïñ¥Ïò®Îã§
        login({ commit }, loginObj) {
            // eslint-disable-line no-unused-vars
            // Î°úÍ∑∏Ïù∏ -> ÌÜ†ÌÅ∞ Î∞òÌôò
            axios
                .post("/auth/signin", loginObj) // postÎ∞©ÏãùÏùÄ requestÎ∞îÎîîÏóê ÌååÎùºÎØ∏ÌÑ∞Î•º ÎÑ£Ïñ¥Ï§Ñ Ïàò ÏûàÎã§
                .then((res) => {
                    console.log("@@ res", res);
                    // ÏÑ±Í≥µÏãú token Ïñ¥Ï©åÍµ¨Í∞Ä ÎÑòÏñ¥ÏôÄ
                    // ÌÜ†ÌÅ∞ÏùÑ Ìó§ÎçîÏóê Ìè¨Ìï®ÏãúÏºúÏÑú Ïú†Ï†Ä Ï†ïÎ≥¥ Î∂àÎü¨ÏôÄ
                    let token = res.data;
                    // ÌÜ†ÌÅ∞ÏùÑ Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•
                    localStorage.setItem("token", token);
                    commit("loginSuccess", token);
                    //   dispatch("getMemberInfo");
                    router.push("/");
                })
                .catch(() => {
                    alert("Ïù¥Î©îÏùºÍ≥º ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÌôïÏù∏ÌïòÏÑ∏Ïöî~!");
                });
        },
        logout({commit}){
            localStorage.removeItem("token")
            commit('logout')
            router.push('/').catch(()=>{});
        },
        async getMemberInfo({ commit }) {
            const myInfo = await getMyInfoApi()
                .then((res) => {
                    console.log("getMemberInfo", res.data);
                    return res.data;
                })
                .catch((err) => {
                    console.error(err);
                    return {};
                });
            commit("setUserInfo"); // Ï†ÑÏó≠ ÏÉÅÌÉúÎ°ú ÏßÄÏ†ïÌïòÍ≥† Ïã∂Îã§Î©¥..
            return myInfo;
        },
        async putMemberInfo() {
            // ÏàòÏ†ï ÏöîÏ≤≠ ÎÑ£Í≥†
            // Ï†ÑÏó≠ ÏÉÅÌÉú Î≥ÄÍ≤Ω
        },
    }
})